<template>
  <div class="selmode">
    <vx-header :shownext="1" :showback="1" @next="next" :title="$t('common.selmode')"></vx-header>
    <div class="vx-container">
      <div class="selmode-modes">
        <div class="selmode-modes_joint" v-if="!isVx" @click="mode=1" :class="{active: mode===1}">{{$t('settings.SplicerMode')}}</div>
        <div class="selmode-modes_switch" @click="mode=2" :class="{active: mode===2}">{{$t('settings.SwitcherMode')}}</div>
      </div>
    </div>
  </div>
</template>
<script>
  import htp from '../api/http';
  import urls from '../api/apiurl';
  import { setLoc, getLoc } from '../utils';
  export default {
    data() {
      return {
        mode: 1,
        isVx: false
      };
    },
    mounted() {
      //获取模式
      let mode = getLoc('setWorkModel');
      const vx6s = [25089, 25090, 25091];
      if(vx6s.some(id => {
        return id == getLoc('modelId');
      })) {
        this.isVx = true;
      }

      if(mode) {
        if(this.isVx) {
          this.mode = 2;
        } else {
          this.mode = mode;
        }
      } else {
        if(this.isVx) {
          this.mode = 2;
        } else {
          this.mode = 1;
        }
      }
      setLoc('setWorkModel', this.mode);
    },
    methods: {
      next() {
        setLoc('mode', this.selected);
        if(!this.isVx) {
          let self = this;
          htp.post(urls.setWorkModel, { "workModel": this.mode }).then(function(res) {
            if(res.status === 10000000) {
              self.$router.push({ 'name': 'scenarist' });
            }
          });
        } else {
          this.$router.push({ 'name': 'scenarist' });
        }
      },
      cancel() {
        this.$router.push({ 'name': 'index' });
      }
    }
  };
</script>

<style lang="less" scoped>
  .selmode {
    .selmode-modes {
      margin-top: 190px;
    }
    .selmode-modes_joint,
    .selmode-modes_switch {
      height: 140px;
      line-height: 140px;
      font-size: 30px;
      margin: 50px 7px;
      color: #ddd;
      opacity: 0.3;
      &.active {
        opacity: 1;
      }
    }
    .selmode-modes_joint {
      background-color: #4c8edf;
    }

    .selmode-modes_switch {
      background-color: #31bb82;
    }
  }
</style>



